.wrapper {
    max-width: 1300px;
    margin: 40px auto;
    margin-bottom: 40px;
}

.mobile {
    /* margin-top: 40px ; */
    margin-bottom: 40px;
    font-size: 10px;
}

.container {
    padding: 10px;
    padding-bottom: 40px;
    /* border-radius: 5px; */
    /* box-shadow: inset 0px 1px 7px #888; */
}

.container_green {
    background: linear-gradient(180deg, rgba(128, 194, 62, 0.51) 0%, rgba(255, 255, 255, 1) 100%);
    /* border: 5px solid rgb(128, 194, 62); */
}

.container_red {
    background: linear-gradient(180deg, rgba(238, 114, 114, 0.8) 0%, rgba(255, 255, 255, 1) 84%);
    /* border: 5px solid rgb(237, 16, 16); */
}

.container_yellow {
    background: linear-gradient(180deg, rgba(255, 242, 125, 1) 0%, rgba(255, 255, 255, 1) 87%);
    /* border: 5px solid rgb(248, 230, 71); */
}

.timeTxt {
    color: #A3A3A3;
    text-align: left;
    /* font-size: 12px; */
}

.titleTxt {
    color: #3A3A3A;
    font-weight: bold;
    text-align: left;
}

.secondTitleTxt {
    color: #3A3A3A;
    font-weight: bold;
    margin-top: 10px;
    text-align: left;
}

.descriptionTxt {
    color: #777777;
    margin-top: 5px;
    text-align: left;
}

.readMore {
    color: #aaa;
    cursor: pointer;
    /* margin-top: 20px; */
}

.yearBtn {
    background-color: #2A3547;
    width: 60px !important;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 30%;
    left: 30%;
    color: #fff;
    border-radius: 10px;
    padding: 2px 20px
}

.title{
    font-weight: bold;
    font-size: 16px;
}

.short_description{
    color:#888;
}