.display_flex_row_center {
    display: flex !important;
    flex-direction: row !important;
    justify-content: center !important;
    align-items: center !important;
}

.display_flex_row_align_center {
    display: flex !important;
    flex-direction: row !important;
    align-items: center !important;
}

.display_flex_row_justify_center {
    display: flex !important;
    flex-direction: row !important;
    justify-content: center !important;
}

.display_flex_column_center {
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    align-items: center !important;
}

.display_flex_column_align_center {
    display: flex;
    flex-direction: column !important;
    align-items: center !important; 
}

.display_flex_column_justify_center {
    display: flex;
    flex-direction: column !important;
    justify-content: center !important;
}

.display_flex_row_space_between {
    display: flex !important;
    justify-content: space-between !important;
    align-items: center;
}

.display_flex_row_space_between_align_first {
    display: flex;
    justify-content: space-between;
}

.display_flex_row_space_around {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.display_flex_row_justify_end {
    display: flex !important;
    justify-content: flex-end !important;
    align-items: center;
}

.display_flex_row_justify_start {
    display: flex !important;
    justify-content: flex-start;
    align-items: center;
}

.numberInput {
    width: 100%;
    border: 1px solid #ddd;
    border-radius: 5px !important;
    padding: 4px 5px 4px 5px!important
}

.numberInput::placeholder {
    color: #bbb
}

.numberInput:focus {
    outline: none!important;
    border: 1px solid #40a9ff;
    box-shadow: 0 0 2px #40a9ff;
    box-shadow: 0 0 0 2px rgb(24 144 255 / 20%)
}

.wrapper {
    box-shadow: 0 0 1px var(--place_holder);
    background-color: var(--grey0);
    border-radius: 10px;
    padding: 20px 63px;
}

.wrapper_dark {
    box-shadow: 0px 1px 4px var(--place_holder_dark);
    background-color: var(--dark_theme1);
    border-radius: 10px;
    padding: 10px 30px
}

.emptyImageTxt {
    font-size: 12px;
    color: var(--dark_text);
    margin-top: 10px;
}

.txt_align_right p, .txt_align_center span {
    text-align: right !important;
}

.txt_align_center p, .txt_align_center span {
    text-align: center !important;
}


@media (max-width:991.98px) {
    .wrapper{
        padding: 10px;
  }
}